        <view class="section-title">ÊàëÁöÑÂ∑•‰Ωú</view>
        <view class="function-grid">
          <view class="function-item primary" @click="scanQRCode">
            <view class="icon-wrapper">
              <text class="icon">ü™ü</text>
            </view>
            <text class="label">Êâ´Á†ÅËøΩÊ∫Ø</text>
            <text class="desc">Êâ´Êèè‰∫åÁª¥Á†ÅÊü•ÁúãËøΩÊ∫Ø‰ø°ÊÅØ</text>
          </view>
          <view class="function-item success" @click="goToProcessInput">
            <view class="icon-wrapper">
              <text class="icon">ü™¨</text>
            </view>
            <text class="label">Â∑•Â∫èÂΩïÂÖ•</text>
            <text class="desc">ÂΩïÂÖ•Â∑•Â∫èÂ∑•Ëâ∫ÂèÇÊï∞</text>
          </view>
        </view>
      </template>
      
      <!-- Ë¥®Ê£ÄÂëòÁïåÈù¢ -->
      <template v-if="userInfo.role === 'QC'">
        <view class="section-title">Ë¥®Ê£ÄÂ∑•‰Ωú</view>
        <view class="function-grid">
          <view class="function-item primary" @click="scanQRCode">
            <view class="icon-wrapper">
              <text class="icon">ü™ü</text>
            </view>
            <text class="label">Êâ´Á†ÅËøΩÊ∫Ø</text>
            <text class="desc">Êâ´Êèè‰∫åÁª¥Á†ÅÊü•ÁúãËøΩÊ∫Ø‰ø°ÊÅØ</text>
          </view>
          <view class="function-item success" @click="goToFluorescentTest">
