            </view>
            <text class="label">ËçßÂÖâÊ£ÄÊµã</text>
            <text class="desc">ËøõË°åÁ¨¨11Ê≠•ËçßÂÖâÊ£ÄÊµã</text>
          </view>
          <view class="function-item warning" @click="goToQCInput">
            <view class="icon-wrapper">
              <text class="icon">‚úÖ</text>
            </view>
            <text class="label">Ë¥®Ê£ÄÂΩïÂÖ•</text>
            <text class="desc">Â°´ÂÜôÊúÄÁªàË¥®Ê£ÄÊä•Âëä</text>
          </view>
          <view class="function-item info" @click="goToBladeList">
            <view class="icon-wrapper">
              <text class="icon">üìã</text>
            </view>
            <text class="label">ÂæÖÊ£ÄÂàóË°®</text>
            <text class="desc">Êü•ÁúãÂæÖË¥®Ê£ÄÂè∂Áâá</text>
          </view>
        </view>
      </template>
      
      <!-- ÁÆ°ÁêÜÂëòÁïåÈù¢ -->
      <template v-if="userInfo.role === 'ADMIN'">
        <view class="section-title">Á≥ªÁªüÁÆ°ÁêÜ</view>
        <view class="function-grid">
          <view class="function-item primary" @click="scanQRCode">
            <view class="icon-wrapper">
              <text class="icon">ü™ü</text>
            </view>
            <text class="label">Êâ´Á†ÅËøΩÊ∫Ø</text>
            <text class="desc">Êâ´Êèè‰∫åÁª¥Á†ÅÊü•ÁúãËøΩÊ∫Ø‰ø°ÊÅØ</text>
          </view>
          <view class="function-item warning" @click="goToCreateBlade">
            <view class="icon-wrapper">
              <text class="icon">üöÅ</text>
            </view>
            <text class="label">ÂàõÂª∫Âè∂Áâá</text>
            <text class="desc">Êñ∞Â¢ûÂè∂ÁâáÂπ∂ÁîüÊàê‰∫åÁª¥Á†Å</text>
          </view>
          <view class="function-item info" @click="goToUserManage">
