            <text class="desc">ÁÆ°ÁêÜÁ≥ªÁªüÁî®Êà∑</text>
          </view>
          <view class="function-item success" @click="goToStatistics">
            <view class="icon-wrapper">
              <text class="icon">üî¢</text>
            </view>
            <text class="label">Êï∞ÊçÆÁªüËÆ°</text>
