            <text class="blade-sn">{{ item.blade_sn }}</text>
            <text class="record-time">{{ formatTime(item.updated_at) }}</text>
          </view>
          <text class="status" :class="getStatusClass(item.status)">
            {{ getStatusText(item.status) }}
          </text>
        </view>
