</template>

<script>
import storage from '@/utils/storage.js'
import { get } from '@/utils/request.js'
import { BLADE_STATUS_MAP } from '@/utils/config.js'

export default {
  data() {
    return {
      userInfo: {},
      recentRecords: []
    }
  },
  computed: {
    roleText() {
      const roleMap = {
        'OPERATOR': 'Êìç‰ΩúÂëò',
        'QC': 'Ë¥®Ê£ÄÂëò',
        'ADMIN': 'ÁÆ°ÁêÜÂëò'
      }
      return roleMap[this.userInfo.role] || 'Êú™Áü•'
    }
  },
  onLoad() {
    this.checkLogin()
  },
  onShow() {
    this.loadRecentRecords()
  },
  methods: {
    checkLogin() {
      const userInfo = storage.getUserInfo()
      if (!userInfo) {
        uni.reLaunch({
          url: '/pages/login/login'
        })
        return
      }
      this.userInfo = userInfo
    },
    
    async loadRecentRecords() {
      try {
        // Ê†πÊçÆËßíËâ≤Âä†ËΩΩ‰∏çÂêåÁöÑËÆ∞ÂΩï
        const url = this.userInfo.role === 'OPERATOR' 
          ? '/blade/my-recent' 
          : '/blade/recent'
        
        const res = await get(url, {
          limit: 10
        }, {
          loading: false  // ‰∏çÊòæÁ§∫Âä†ËΩΩÊèêÁ§∫
        })
        
        if (res && res.data) {
          this.recentRecords = res.data.list || []
        }
      } catch (error) {
        console.error('Âä†ËΩΩÊúÄËøëËÆ∞ÂΩïÂ§±Ë¥•:', error)
        // ‰∏çÊòæÁ§∫ÈîôËØØÊèêÁ§∫ÔºåÈÅøÂÖçÂπ≤Êâ∞Áî®Êà∑‰ΩìÈ™å
        this.recentRecords = []
      }
    },
    
    scanQRCode() {
      uni.navigateTo({
        url: '/pages/scan/scan'
      })
    },
    
    goToProcessInput() {
      // Â∑•Â∫èÂΩïÂÖ•ÂäüËÉΩÔºöÂÖàÊâ´Á†ÅÈÄâÊã©Âè∂ÁâáÔºåÂÜçÂΩïÂÖ•Â∑•Â∫è
      uni.navigateTo({
        url: '/pages/scan/scan?mode=process-input'
      })
    },
    
    goToFluorescentTest() {
      // ËçßÂÖâÊ£ÄÊµãÔºöÂÖàÊâ´Á†ÅÈÄâÊã©Âè∂ÁâáÔºåÂÜçÂΩïÂÖ•Á¨¨11Ê≠•Â∑•Â∫è
      uni.navigateTo({
        url: '/pages/scan/scan?mode=process-input&processCode=FLUORESCENT_TEST'
      })
    },
    
    goToQCInput() {
      uni.navigateTo({
        url: '/pages/qc-input/qc-input'
      })
    },
    
    goToBladeList() {
      uni.navigateTo({
        url: '/pages/blade-list/blade-list'
      })
    },
    
    goToCreateBlade() {
      uni.navigateTo({
        url: '/pages/blade-create/blade-create'
      })
    },
    
    goToUserManage() {
      uni.navigateTo({
        url: '/pages/user-manage/user-manage'
      })
    },
    
    goToStatistics() {
      uni.navigateTo({
        url: '/pages/statistics/statistics'
      })
    },
    
    goToYOLODetect() {
      uni.navigateTo({
        url: '/pages/yolo-detect/yolo-detect'
      })
    },
    
    viewTrace(bladeId) {
      uni.navigateTo({
        url: `/pages/trace-detail/trace-detail?bladeId=${bladeId}`
      })
    },
    
    handleLogout() {
      uni.showModal({
        title: 'ÊèêÁ§∫',
        content: 'Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÁôªÂΩïÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            storage.clearAll()
            uni.reLaunch({
              url: '/pages/login/login'
            })
          }
        }
      })
    },
    
    getStatusText(status) {
      return BLADE_STATUS_MAP[status] || status
    },
    
    getStatusClass(status) {
      const classMap = {
        'NEW': 'status-new',
        'IN_PROCESS': 'status-processing',
        'BLOCKED': 'status-blocked',
        'READY_FOR_QC': 'status-ready',
        'COMPLETED': 'status-completed',
        'SCRAPPED': 'status-scrapped'
      }
      return classMap[status] || ''
    },
    
    formatTime(time) {
      if (!time) return ''
      const date = new Date(time)
      const now = new Date()
      const diff = now - date
      const minutes = Math.floor(diff / 60000)
      
      if (minutes < 1) return 'ÂàöÂàö'
      if (minutes < 60) return `${minutes}ÂàÜÈíüÂâç`
      if (minutes < 1440) return `${Math.floor(minutes / 60)}Â∞èÊó∂Ââç`
      return `${Math.floor(minutes / 1440)}Â§©Ââç`
    }
  }
}
</script>

<style scoped>
