}

.record-info {
  display: flex;
  flex-direction: column;
}

.blade-sn {
  font-size: 32rpx;
  font-weight: bold;
  color: #333333;
  margin-bottom: 10rpx;
}

.record-time {
  font-size: 24rpx;
  color: #999999;
}

.status {
  font-size: 24rpx;
  padding: 8rpx 16rpx;
  border-radius: 20rpx;
}

.status-new {
